.info {
  display: flex;
  gap: 60px;
  margin-bottom: 25px;
  text-shadow: 2px 2px 0px rgba(0, 0, 0, 1);
  color: white;
}

.title {
  text-shadow: 4px 4px 0px rgba(0, 0, 0, 0.5);
  font-size: 70px;
  margin-bottom: 25px;
  color: white;
}

.new {
  text-shadow: 2px 2px 0px rgba(0, 0, 0, 1);
  color: white;
}

.description {
  text-shadow: 2px 2px 0px rgba(0, 0, 0, 1);
  width: calc(100% - 300px);
  text-align: center;
  margin-bottom: 50px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400px;
  font-size: 23px;
  color: white;
}

.actionButtons {
  display: flex;
  gap: 30px;
}

.button {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 20px;
  background: #ff0000;
  border: none;
  padding: 10px 35px;
  color: white;
  display: flex;
  align-items: center;
  gap: 5px;
  text-decoration: none;
  color: white;
}

.button:hover {
  background: #000;
  transition: 0.3s all ease-in-out;
  color: white;
}

.backgroundImage {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  mask-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.65) 100%
  );
  -webkit-mask-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.65) 100%
  );
  z-index: 1;
  position: absolute;
  filter: blur(6px);
}

.content {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 120px;
}

.wrapper {
  width: 100%;
  height: 80vh;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

